'use strict'

var Validation = require('rgui-ui-field').Validation;

import Base from '../../../base/component';

var bowser = require('bowser');

var Input = Base.extend({
    config() {
        this.defaults({
            value: '',
            type: 'text',
            name: '',
            disabled: 0,
            placeholder: '',
            size:'large',
            state: '',
            tip:'',
            _tip:'',
            enableDelete: 1,
            deleteAutoFocus: 1,
            rules: [],
            del_icon_clazz:'i-icon i-icon-delete',
            blurDelay: 0,
            _eltIE9: bowser.msie && bowser.version <= 9
            // _eltIE9: 1
        });
        this.supr();  
        // validation$outer
        // TODO $outervalidation$outer
        let $outer = this.$outer;
        while ($outer) {
            if ($outer instanceof Validation) {
            $outer.fields.push(this);
            break;
            }
            $outer = $outer.$outer;
        }
    },
    init(){
        // this.$watch('value',(value,oldValue)=>{
        //     if(value !== oldValue && this.data.state === 'error'){
        //         this.clearError();
        //     }
        // });
    },
    destroy() {
        if (this.$outer && this.$outer instanceof Validation) {
            // $outer
            const index = this.$outer.fields.indexOf(this);
            ~index && this.$outer.fields.splice(index, 1);
        }
        this.supr();
    },
    validate(trigger = 'submit') {

        const value = this.data.value;
        const rules = this.data.rules.filter((rule) => (rule.trigger + '+submit').includes(trigger));

        // this.data._state = this.data.state = 'validating';
        // _state
        this.data.state = 'validating';

        Validation.validate(value, rules, (result) => {
            if (result.firstRule && !(result.firstRule.mute || '').includes(trigger))
                // @TODO tip
                this.data._tip = result.message;
            else
                this.data._tip = '';
            // _state
            // this.data._state = this.data.state = result.success ? 'success' : 'error';
            this.data.state = result.success ? 'success' : 'error';
            
            this.$update();

            /**
             * @event validate 
             * @property {object} sender 
             * @property {string} trigger 
             * @property {boolean} success 
             * @property {string} message 
             * @property {object} firstRule 
             */
            this.$emit('validate', Object.assign({
                sender: this,
                trigger,
            }, result));
        });
    },
    focus() {
        this.$refs.input.focus();
    },
    blur() {
        this.$refs.input.blur();
    },
    getValue() {
        return this.$refs.input.value;
    },
    setValue(_value) {
        this.$refs.input.value = _value||'';
    },
    setState(_state){
        this.data.state = _state||''
    },
    getState(){
        return this.data.state;
    },
    showError(_tip) {
        this.data.state = 'error';
        this.data._tip = _tip;
        this.$update();
    },
    clearError() {
        if(this.data.state === 'error'){
            this.data.state = '';
            this.data._tip = '';
        }
        this.$update();
    },
    _onFocus(_event) {
        this.$emit('focus',_event);
        // this.data._tip = this.data.tip;
        this.$update();
        
    },
    _onBlur(_event) {
        var _blur = this.data.blurDelay>34?this.data.blurDelay:34;

        this.$emit('blur',_event);
        if(this.data.blurDelay){
            setTimeout(()=>{
                if(this.data.value === ''){
                    return;
                }
                this.validate('blur');
            },_blur);
        }else{
             if(this.data.value === ''){
                return;
             }
            this.validate('blur');
        }
    },
    _onInput(_event){
        this.$emit('input', _event);
        if((_event.event && _event.event.propertyName === 'value')||_event.type=='input'){
            this.clearError();
        }
        // TODO _compositionInputing
        // setTimeout(() => !this.data._compositionInputing && this.validate('input'));
        // this.validate('input');
    },
    _onDelete(_event){
        
        this.data.value = '';
        this.clearError();
        // focus
        if(!!this.data.deleteAutoFocus){
            this.focus();
            
        }
    }
});

module.exports = Input;




